<template>
  <v-app @drop.native.prevent="dropFiles" @dragover.native.prevent @dragleave.native.prevent>
    <v-app-bar app color="primary" dark>
      <v-app-bar-nav-icon></v-app-bar-nav-icon>

      <v-toolbar-title>Session Viewer</v-toolbar-title>

      <v-spacer></v-spacer>

      <v-btn href="https://github.com/vuetifyjs/vuetify/releases/latest" target="_blank" text>
        <span class="mr-2">Latest Release</span>
        <v-icon>mdi-open-in-new</v-icon>
      </v-btn>
    </v-app-bar>

    <v-content>
      <v-container v-if="session" fluid fill-height style="align-items:start">
        <v-row no-gutters>
          <v-col>
            <v-card color="primary" dark>
              <v-card-title>
                {{ session.name }}
                <v-spacer></v-spacer>
                <v-text-field
                  v-model="session.search"
                  :append-icon="session.search ? 'mdi-close':''"
                  append-outer-icon="mdi-magnify"
                  label="Search"
                  single-line
                  hide-details
                  @click:append="session.search = ''"
                ></v-text-field>
              </v-card-title>
              <v-tabs v-model="window" center-active background-color="primary" dark>
                <v-tab
                  v-for="(item, key, index) in session.windows"
                  :key="key"
                >Window {{index+1}} ({{getFilteredTabCount(key)}})</v-tab>
              </v-tabs>
              <v-tabs-items v-model="window">
                <v-tab-item
                  v-for="(item, key) in session.windows"
                  :key="key"
                  :transition="false"
                  :reverse-transition="false"
                  :eager="true"
                >
                  <v-card flat>
                    <v-data-table
                      :footer-props="footerProps"
                      :items-per-page="50"
                      :headers="headers"
                      :items="getFilteredTabs(key)"
                    >
                      <template v-slot:item.favIconUrl="{ item }">
                        <img v-if="item.favIconUrl" :src="item.favIconUrl" width="16" height="16" />
                      </template>
                      <template v-slot:item.url="{ item }">
                        <a :href="item.url" target="_blank">{{ item.url }}</a>
                      </template>
                      <template
                        v-slot:item.lastAccessed="{ item }"
                      >{{new Date(item.lastAccessed).toISOString()}}</template>
                    </v-data-table>
                  </v-card>
                </v-tab-item>
              </v-tabs-items>
            </v-card>
          </v-col>
        </v-row>
      </v-container>
      <v-container v-else fluid fill-height style="align-items:center">
        <v-row no-gutters justify="center" align="center">
          <span>Drop Firefox session file here.</span>
        </v-row>
      </v-container>
    </v-content>
  </v-app>
</template>

<script>
//import HelloWorld from "./components/HelloWorld";

export default {
  name: "App",

  components: {
    //HelloWorld
  },

  data: () => ({
    session: null,
    window: null,
    headers: [
      {
        text: "Icon",
        sortable: false,
        value: "favIconUrl"
      },
      {
        text: "Title",
        align: "start",
        sortable: false,
        value: "title"
      },
      { text: "URL", value: "url" },
      { text: "Last Accessed", value: "lastAccessed" }
    ],
    footerProps: {
      "items-per-page-options": [10, 20, 50, -1]
    }
  }),

  methods: {
    getFilteredTabs(key) {
      //function search(x, y) {
      //  return x.includes(y);
      //}
      function search(x, y) {
        return x.toLowerCase().includes(y.toLowerCase());
      }
      let result = Object.values(this.session.windows[key]);
      if (this.session.search) {
        result = result.filter(
          item =>
            search(item.title, this.session.search) ||
            search(item.url, this.session.search)
        );
      }
      this.session.tabCounts[key] = result.length;
      return result;
    },
    getFilteredTabCount(key) {
      return this.session.tabCounts[key];
    },
    loadSession(file) {
      //console.log(file);
      var reader = new FileReader();

      // Closure to capture the file information.
      reader.onload = e => {
        let data = JSON.parse(e.target.result);

        let windows = data[0].windows;

        let tabCounts = {};
        for (let key of Object.keys(windows)) {
          tabCounts[key] = Object.keys(windows[key]).length;
        }

        this.session = {
          name: file.name,
          search: "",
          windows,
          tabCounts
        };
      };

      // Read in the image file as a data URL.
      reader.readAsText(file);
    },

    dropFiles(event) {
      let droppedFiles = event.dataTransfer.files;

      if (!droppedFiles) return;

      let file = droppedFiles[0];

      this.loadSession(file);
    }
  }
};
</script>
